'use client';
import React, { useState } from 'react';
import { Mail, Phone, MapPin, Twitter, Linkedin, Discord } from 'lucide-react';
import DiscordIcon from '@/components/icons/Discord';
const contactDetails = [
  {
    icon: Mail,
    label: 'Email',
    value: 'contact@yourcompany.com',
  },
  {
    icon: Phone,
    label: 'Phone',
    value: '+1 (555) 123-4567',
  },
  {
    icon: MapPin,
    label: 'Location',
    value: 'San Francisco, CA',
  },
];

const socialLinks = [
  {
    icon: Discord,
    href: 'https://discord.gg/kJrvdhmzVd',
  },
  {
    icon: Twitter,
    href: '#',
  },
  {
    icon: Linkedin,
    href: '#',
  },
];

const Input = ({ id, label, type = 'text', value, onChange, placeholder }) => (
  <div>
    <label htmlFor={id} className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
      {label}
    </label>
    <input
      type={type}
      id={id}
      name={id}
      value={value}
      onChange={onChange}
      required
      placeholder={placeholder}
      className="w-full px-4 py-3 text-sm sm:text-base rounded-xl border border-gray-300 dark:border-gray-600 bg-white/50 dark:bg-gray-800/50 text-gray-900 dark:text-white placeholder-gray-500 dark:placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-[#DC143C] transition duration-300"
    />
  </div>
);

const ContactUs = () => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    subject: '',
    message: '',
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log('Submitted:', formData);
    setFormData({ name: '', email: '', subject: '', message: '' });
  };

  return (
    <main className="relative">
      <div className="container mx-auto px-4 pt-32 pb-12 sm:pt-24 sm:pb-16 lg:pt-32 lg:pb-20 xl:pt-[220px]">
        <div className="text-center mb-12">
          <h1 className="text-4xl sm:text-5xl font-bold text-gray-800 dark:text-white tracking-tight mb-4">
            Get in{' '}
            <span className="bg-gradient-to-r from-[#DC143C] to-[#003893] text-transparent bg-clip-text">
              Touch
            </span>
          </h1>
          <p className="text-gray-600 dark:text-gray-300 text-base sm:text-lg max-w-2xl mx-auto">
            Have questions or want to collaborate? We'd love to hear from you.
          </p>
        </div>

        <div className="flex flex-col xl:flex-row gap-12">
          {/* Contact Info */}
          <div className="w-full xl:w-1/2 space-y-8">
            <div>
              <h2 className="text-2xl font-bold text-gray-800 dark:text-white mb-6">Contact Information</h2>
              <div className="space-y-6">
                {contactDetails.map(({ icon: Icon, label, value }) => (
                  <div
                    key={label}
                    className="flex items-center gap-4 p-4 rounded-xl bg-white/10 backdrop-blur-lg border border-gray-200 dark:border-gray-700 hover:scale-105 transition duration-300"
                  >
                    <div className="w-12 h-12 bg-gradient-to-r from-[#DC143C] to-[#003893] rounded-lg flex items-center justify-center">
                      <Icon className="text-white w-5 h-5" />
                    </div>
                    <div>
                      <h3 className="font-semibold text-gray-800 dark:text-white">{label}</h3>
                      <p className="text-gray-600 dark:text-gray-300 break-all">{value}</p>
                    </div>
                  </div>
                ))}
              </div>
            </div>

            <div>
              <h3 className="text-xl font-semibold text-gray-800 dark:text-white mb-4">Follow Us</h3>
              <div className="flex gap-4">
                {socialLinks.map(({ icon: Icon, href }, idx) => (
                  <a
                    key={idx}
                    href={href}
                    target="_blank"
                    rel="noreferrer"
                    className="w-12 h-12 bg-gradient-to-r from-[#DC143C] to-[#003893] rounded-lg flex items-center justify-center hover:scale-110 transition"
                  >
                    <Icon className="text-white w-5 h-5" />
                  </a>
                ))}
              </div>
            </div>
          </div>

          {/* Contact Form */}
          <div className="w-full xl:w-1/2">
            <form
              onSubmit={handleSubmit}
              className="relative rounded-2xl backdrop-blur-lg p-6 lg:p-8 shadow-xl border border-gray-200 dark:border-gray-700"
            >
              <h2 className="text-2xl font-bold text-gray-800 dark:text-white mb-6">Send us a Message</h2>

              <div className="space-y-6">
                <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
                  <Input
                    id="name"
                    label="Full Name"
                    value={formData.name}
                    onChange={handleChange}
                    placeholder="John Doe"
                  />
                  <Input
                    id="email"
                    type="email"
                    label="Email Address"
                    value={formData.email}
                    onChange={handleChange}
                    placeholder="john@example.com"
                  />
                </div>

                <Input
                  id="subject"
                  label="Subject"
                  value={formData.subject}
                  onChange={handleChange}
                  placeholder="How can we help you?"
                />

                <div>
                  <label htmlFor="message" className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                    Message
                  </label>
                  <textarea
                    id="message"
                    name="message"
                    value={formData.message}
                    onChange={handleChange}
                    rows={6}
                    required
                    className="w-full px-4 py-3 rounded-xl border border-gray-300 dark:border-gray-600 bg-white/50 dark:bg-gray-800/50 text-gray-900 dark:text-white placeholder-gray-500 dark:placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-[#DC143C] transition duration-300 resize-none"
                    placeholder="Tell us about your project or inquiry..."
                  />
                </div>

                <button
                  type="submit"
                  className="w-full bg-gradient-to-r from-[#DC143C] to-[#003893] text-white py-4 px-6 rounded-xl font-semibold hover:scale-105 hover:shadow-lg transition duration-300"
                >
                  Send Message
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </main>
  );
};

export default ContactUs;
